import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { MultilevelMenuComponent } from '../components/multilevel-menu/multilevel-menu';

import { BrowserAnimationsModule, NoopAnimationsModule } from '@angular/platform-browser/animations';
import { SidebarMenuProvider } from '../providers/sidebar-menu/sidebar-menu';
import { HttpModule } from '@angular/http';
import { environment } from '../environments/environment';
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';

// import { ImageSliderProvider } from '../providers/image-slider/image-slider';
// import { HomeSliderProvider } from '../providers/home-slider/home-slider';
// import { ManageHomeSliderComponent } from '../components/manage-home-slider/manage-home-slider';
// import { UploadComponent } from '../components/upload/upload';

@NgModule({
  declarations: [
    MyApp,
    MultilevelMenuComponent,
    // ManageHomeSliderComponent,
    // UploadComponent
  ],
  imports: [
    HttpModule,
    BrowserModule,
    BrowserAnimationsModule,
    NoopAnimationsModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/administrative/administrative.module.ngfactory#AdministrativePageModuleNgFactory', name: 'Administrative', segment: 'Administrative', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/bluegrass-calendar/bluegrass-calendar.module.ngfactory#BluegrassCalendarModuleNgFactory', name: 'BluegrassCalendar', segment: 'bluegrass-calendar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/bluegrass/bluegrass.module.ngfactory#BluegrassModuleNgFactory', name: 'Bluegrass', segment: 'bluegrass', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contact/contact.module.ngfactory#ContactModuleNgFactory', name: 'Contact', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/history/history.module.ngfactory#HistoryModuleNgFactory', name: 'History', segment: 'history', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomeModuleNgFactory', name: 'Home', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/main-calendar/main-calendar.module.ngfactory#MainCalendarModuleNgFactory', name: 'MainCalendar', segment: 'main-calendar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/services/services.module.ngfactory#ServicesModuleNgFactory', name: 'Services', segment: 'services', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/youth/youth.module.ngfactory#YouthModuleNgFactory', name: 'Youth', segment: 'youth', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule, // imports firebase/database, only needed for database features
    AngularFireAuthModule, // imports firebase/auth, only needed for auth features
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    SidebarMenuProvider,
    // ImageSliderProvider,
    // HomeSliderProvider,
  ]
})
export class AppModule { }



